.sidebar {
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 4rem 0;
}

.sidebar > a:first-child {
  padding: 0;
}
.sidebar > a > img {
  width: 100%;
  transition: all 0.5s;
}

.sidebar > a > img:hover {
  filter: drop-shadow(0 0 10px tomato);
}
.sidebar a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.493);
  font: 200 1rem "Roboto";
  padding: 3.5vw;
  transition: all 0.5s;
}
.sidebar > div {
  transition: all 0.5s;
  color: rgba(0, 0, 0, 0.493);
  font: 200 1.2rem "Roboto";
  margin: 3vw;
}
.sidebar a:hover {
  color: tomato;
  transform: scale(1.1);
}
.sidebar a:first-child:hover {
  transform: none;
}
.sidebar > div:hover {
  color: tomato;
  transform: scale(1.1);
}
.sidebar a > P {
  display: flex;
  align-items: center;
}
.sidebar a > p > svg {
  margin-right: 0.5rem;
}
.sidebar div > p > svg {
  margin-right: 0.5rem;
}
.MuiTypography-root {
  background-color: #fff !important;
}